@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{box-sizing:border-box;font-family:Raleway, sans-serif;width:100%}button,a{border-radius:0.4em;border:none;color:#f0f0f0;font-family:inherit;outline:none;padding:0.5em 1.3em;text-decoration:none;transition:all 1s;-webkit-user-select:none;-ms-user-select:none;user-select:none}button:focus,a:focus{box-shadow:0.2em 0.2em 0.2em gray}#wrapper{height:100vh;transition:background-color 1s;width:100%}#wrapper #quote-box{background-color:#f0f0f0;border-radius:0.5em;left:50%;max-width:35em;min-width:13em;padding:2em;position:absolute;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);transition:all 1s}#wrapper #quote-box #quote{font-size:1.3em;line-height:1.2em;text-align:center;transition:color 1s ease;width:100%}@media screen and (max-width: 599px){#wrapper #quote-box #quote{font-size:1em;line-height:1em}}#wrapper #quote-box #author{padding:1em;text-align:right;transition:color 1s ease}#wrapper #quote-box #buttons{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin-top:0.5em}

